import React, { useContext, useEffect, useRef, useState } from "react";
import invariant from "tiny-invariant";
import { Device, types } from "mediasoup-client";
import { Room } from "colyseus.js";
import { ICroozRoomState } from "@crooz/shared-types";
import { Consumer } from "mediasoup-client/lib/Consumer";
import { Transport, TransportOptions } from "mediasoup-client/lib/Transport";
import { WebRtcTransport } from "mediasoup/lib/WebRtcTransport";
import { TransportConfig } from "../App";
import { MediaKind, RtpParameters } from "mediasoup-client/lib/RtpParameters";

type Mediasoup = {};

export const MediasoupContext = React.createContext<Mediasoup | null>(null);

interface Props {
  serverReceiveId: string;
  room: Room;
  microphone: MediaStreamTrack;
}

export const MediasoupProvider: React.FC<Props> = ({
  children,
  serverReceiveId,
  room,
  microphone,
}) => {
  const deviceRef = useRef(new Device());
  const receiveTransport = useRef<Transport | null>(null);
  const sendTransport = useRef<Transport | null>(null);
  const consumerMap = useRef(new Map<string, Consumer>());
  const [ids, setIds] =
    useState<{ producer: string; consumer: string } | null>(null);
  const [transportConfig, setTransportConfig] =
    useState<TransportConfig | null>(null);

  useEffect(() => {
    room?.onMessage("transport-config", async (cfg) => {
      setTransportConfig(JSON.parse(cfg));
    });
  }, [room]);

  useEffect(() => {
    room?.onMessage("create-consumer", async (msg) => {
      const cfg: {
        producerId: string;
        id: string;
        kind: MediaKind;
        rtpParameters: RtpParameters;
        clientId: string;
      } = JSON.parse(msg);
      // create a consumer
      if (cfg.clientId !== room.sessionId) {
        return;
      }
      const consumer = await receiveTransport.current?.consume(cfg);
      if (consumer) {
        const { track } = consumer;
        const speaker = document.getElementById(
          "remoteAudio"
        ) as HTMLAudioElement;
        if (speaker) {
          if (speaker.srcObject) {
            (speaker.srcObject as MediaStream).addTrack(track);
          } else {
            speaker.srcObject = new MediaStream([track]);
          }
        }
      }
    });
  }, [room]);

  useEffect(() => {
    room.onMessage("transport-ids", (msg) => {
      setIds(JSON.parse(msg));
    });
    room?.send("mediaSoupJoin", "");
  }, [room]);

  useEffect(() => {
    room.onStateChange((state: ICroozRoomState) => {
      // see if there are new drivers in the room
      state.drivers.forEach((value, clientId) => {
        if (clientId !== room.sessionId) {
          console.log("new driver detected", clientId);
        }
      });

      // see if drivers have left
      consumerMap.current.forEach((value, clientId) => {
        if (!state.drivers.has(clientId)) {
          consumerMap.current.delete(clientId);
        }
      });
    });
  });

  useEffect(() => {
    const setup = async (cfg: TransportConfig) => {
      const { producer, consumer } = cfg;
      const sendTransportOptions: TransportOptions = {
        ...producer,
        id: ids?.producer || "", //change this
      };

      const receiveTransportOptions: TransportOptions = {
        ...consumer,
        id: ids?.consumer || "", //change this
      };

      if (!deviceRef.current.loaded) {
        await deviceRef.current.load({
          routerRtpCapabilities: producer.rtpCapabilities,
        });
        room.send(
          "device-rtp-capabilities",
          JSON.stringify(deviceRef.current.rtpCapabilities)
        );
      } else {
        return;
      }

      // create client side producer transport
      sendTransport.current =
        deviceRef.current.createSendTransport(sendTransportOptions);

      receiveTransport.current = deviceRef.current.createRecvTransport(
        receiveTransportOptions
      );

      // when client side producer is connected
      // send the client dtls-params
      sendTransport.current.on(
        "connect",
        async ({ dtlsParameters }, callback, errback) => {
          // Signal local DTLS parameters to the server side transport.
          try {
            await room.send(
              "producer-transport-connect",
              JSON.stringify({
                transportId: sendTransport.current?.id,
                dtlsParameters: dtlsParameters,
              })
            );
            // Tell the transport that parameters were transmitted.
            callback();
          } catch (error) {
            // Tell the transport that something was wrong.
            errback(error);
          }
        }
      );

      receiveTransport.current.on(
        "connect",
        async ({ dtlsParameters }, callback, errback) => {
          // Signal local DTLS parameters to the server side transport.
          try {
            await room.send(
              "consumer-transport-connect",
              JSON.stringify({
                transportId: receiveTransport.current?.id,
                dtlsParameters: dtlsParameters,
              })
            );
            // Tell the transport that parameters were transmitted.
            callback();
          } catch (error) {
            // Tell the transport that something was wrong.
            errback(error);
          }
        }
      );

      sendTransport.current.on(
        "produce",
        async (parameters, callback, errback) => {
          // Signal parameters to the server side transport and retrieve the id of
          // the server side new producer.
          room.onMessage("producerId", (id) => {
            callback({ id });
          });
          await room.send(
            "create-producer",
            JSON.stringify({
              transportId: sendTransport.current?.id,
              kind: parameters.kind,
              rtpParameters: parameters.rtpParameters,
              appData: parameters.appData,
            })
          );

          // Let's assume the server included the created producer id in the response
          // data object.
        }
      );

      // here we generate the client side producer
      await sendTransport.current.produce({
        track: microphone,
      });
    };
    if (transportConfig) {
      ids && setup(transportConfig);
    }
  }, [room, serverReceiveId, microphone, transportConfig, ids]);

  return (
    <MediasoupContext.Provider value={null}>
      {children}
    </MediasoupContext.Provider>
  );
};
