import React from "react";
import { Button, TextInput } from "grommet";
import { useColyseus } from "../utils/colyseus-context";

export const JoinScreen = (): JSX.Element => {
  const { roomCode, setRoomCode, joinRoom, createRoom, username, setUsername } =
    useColyseus();

  return (
    <div>
      <TextInput
        placeholder="Room Code"
        value={roomCode}
        onChange={(event) => setRoomCode(event.target.value)}
      />
      <TextInput
        placeholder="Username"
        value={username}
        onChange={(event) => setUsername(event.target.value)}
      />
      <Button
        primary
        label="Join Cruise"
        onClick={() => {
          joinRoom();
        }}
      />
      <Button
        primary
        label="Create Cruise"
        onClick={() => {
          createRoom();
        }}
      />
    </div>
  );
};
