import { Room } from "colyseus.js";
import { Device, types } from "mediasoup-client";
import { RtpCapabilities } from "mediasoup-client/lib/RtpParameters";
import { TransportOptions } from "mediasoup-client/lib/Transport";
import React, { useEffect, useMemo, useRef, useState } from "react";
import "./App.css";
import { JoinScreen } from "./Components/JoinScreen";
import { ColyseusProvider, useColyseus } from "./utils/colyseus-context";
import { MediasoupProvider } from "./utils/mediasoup-context";

function App() {
  return (
    <ColyseusProvider>
      <h1>you are in the app</h1>
      <AppView />
    </ColyseusProvider>
  );
}

export type TransportConfig = {
  consumer: TransportOptions & { rtpCapabilities: RtpCapabilities };
  producer: TransportOptions & { rtpCapabilities: RtpCapabilities };
};

const AppView = (): JSX.Element => {
  const colyseus = useColyseus();
  const { connected, roomCode, room, roomState } = colyseus;

  const drivers = useMemo(() => {
    if (roomState) {
      return Array.from(roomState.drivers).map(([key, driver]) => (
        <div key={key}>{driver.username}</div>
      ));
    }
    return [];
  }, [roomState]);

  const [microphone, setMicrophone] = useState<MediaStreamTrack | null>(null);

  useEffect(() => {
    const getMic = async () => {
      const stream = await navigator.mediaDevices.getUserMedia({
        audio: true,
      });
      const microphone = stream.getTracks()[0];

      setMicrophone(microphone);
    };
    getMic();
  }, []);

  return (
    <>
      {microphone && (
        <div className="App">
          {connected && room ? (
            <MediasoupProvider
              room={room}
              serverReceiveId={roomState?.receiveTransportId || ""}
              microphone={microphone}
            >
              <h1>create offer</h1>
              <div>you are connected to {roomCode} with state:</div>
              <pre>{JSON.stringify(room?.state, undefined, 2)}`</pre>
              <div>drivers: {drivers}</div>
              <audio
                id="remoteAudio"
                autoPlay={true}
                playsInline={true}
              ></audio>
            </MediasoupProvider>
          ) : (
            <JoinScreen />
          )}
        </div>
      )}
    </>
  );
};

export default App;
