import { Client, Room } from "colyseus.js";
import React, { useContext, useEffect, useRef, useState } from "react";
import {
  ICroozRoomState,
  joinRoom as joinRoomAction,
} from "@crooz/shared-types";
import { getType } from "typesafe-actions";
import invariant from "tiny-invariant";
import axios from "axios";

const ROOM_NAME = "CroozRoom";

type Colyseus = {
  client: Client;
  room: Room | null;

  roomCode: string;
  setRoomCode: React.Dispatch<React.SetStateAction<string>>;

  joinRoom: () => void;
  createRoom: () => void;
  connected: boolean;

  roomState: ICroozRoomState | null;

  username: string;
  setUsername: React.Dispatch<React.SetStateAction<string>>;
  serverReceiveId: string;
};

export const ColyseusContext = React.createContext<Colyseus | null>(null);

export const ColyseusProvider: React.FC = ({ children }) => {
  const client = useRef<Client>(
    new Client(`ws://${process.env.REACT_APP_COLYSEUS_IP}`)
  );
  const [roomCode, setRoomCode] = useState<string>("");
  const [connected, setConnect] = useState<boolean>(false);
  const [room, setRoom] = useState<Room | null>(null);
  const [roomState, setRoomState] = useState<ICroozRoomState | null>(null);
  const [username, setUsername] = useState<string>("");
  const [serverReceiveId, setServerReceiveId] = useState<string>("");

  useEffect(() => {
    if (!room) {
      return;
    }
    setRoomCode(room.id);
    room.send(getType(joinRoomAction), joinRoomAction({ username }).payload);
  }, [room, username]);

  const setRoomAndSubscribe = (room: Room) => {
    room.onStateChange((state) => {
      setRoomState({ ...state });
    });
    setRoom(room);
  };

  const joinRoom = async () => {
    if (!roomCode) {
      return;
    }
    try {
      if (room) {
        room.leave();
      }
      const res = await axios.get(
        `http://${process.env.REACT_APP_COLYSEUS_IP}/rooms?roomCode=${roomCode}`
      );
      setRoomAndSubscribe(await client.current.joinById(res.data));
      setConnect(true);
    } catch (e) {
      setConnect(false);
    }
  };

  const createRoom = async () => {
    try {
      if (room) {
        room.leave();
      }
      const createdRoom = await client.current.create(ROOM_NAME);
      setRoomAndSubscribe(createdRoom);
      setConnect(true);
    } catch (e) {
      setConnect(false);
    }
  };

  const colyseus: Colyseus = {
    client: client.current,
    roomCode,
    setRoomCode,
    joinRoom,
    createRoom,
    connected,
    room,
    username,
    setUsername,
    roomState,
    serverReceiveId,
  };

  return (
    <ColyseusContext.Provider value={colyseus}>
      {children}
    </ColyseusContext.Provider>
  );
};

export const useColyseus = (): Colyseus => {
  const colyseus = useContext(ColyseusContext);

  invariant(colyseus, "no context provide for colyseus");

  return colyseus;
};
